import React from 'react';
import c from './Footer.module.css';
import {setTheme} from "../../redux/app_reducer";
import {connect} from "react-redux";


const Footer = ({setTheme}) => {
    let nightThemeSet = () =>{
        setTheme('night')
    }
    let dayThemeSet = () =>{
        setTheme('day')
    }
  return (
    <footer>
        <div className={c.theme}>
            <span className="day" onClick={dayThemeSet}>день</span> / <span className="night" onClick={nightThemeSet}>ніч</span>
        </div>
      <div className={c.copy}>
        &copy; wd<br />
        * 2011-2024 *
      </div>
    </footer>
  );
}

const mapStateToProps = ({app}) => {
    return {
        app: app
    }
};

export default connect(mapStateToProps,{setTheme})(Footer);
// export default Footer;
